* {
  box-sizing: border-box;
}


body {
  margin: 0;
  padding: 0;
  font-family: 'Avenir Next', sans-serif;
  background-color: black;
}


h1,
h2,
h3,
h4,
p {
  margin: 0;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 650px;
  height: 400px;
  padding-left: 10px;
  margin: 50px auto;
  background-color: #008724;
  border-radius: 10px;
  font-family: 'Press Start 2P', cursive;
  font-size: 12px
}

.textBox {
    position: relative;
    width: 90%;
    height: 30%;
    min-height: 30%;
    max-height:30%;
    /* border: 1px solid black; */
    color: white
}

.UserEntry {
    position: relative;
    width: 90%;
    height: 20%;
    min-height: 20%;
    max-height:20%;
    color: white;
    /* border: 1px solid black; */
}

.UserInput {
  width: 50%;
  background-color: #008724;
  border: none;
  color: white;
  font-family: 'Press Start 2P', cursive;
}

.info {
    position: relative;
    width: 90%;
    height: 20%;
    min-height: 20%;
    max-height:20%;
    /* border: 1px solid black; */
    color: white
}

